.coursemap-title {
  color: #838383; }

.card.coursemap-listing {
  position: relative;
  border-radius: 10px;
  padding: 4rem 0;
  border-color: #77B02A;
  background: #fafafa; }
  .card.coursemap-listing--active {
    background: #77B02A; }
    .card.coursemap-listing--active .coursemap-listing__title {
      color: white; }
    .card.coursemap-listing--active .coursemap-listing__subtext {
      color: #e3f3cd; }
    .card.coursemap-listing--active .coursemap-listing__number {
      border: 1px solid white;
      color: white; }
    .card.coursemap-listing--active .coursemap-listing__btn {
      border-color: #3f5e16;
      background: #3f5e16;
      padding: 0.5rem 2rem; }

.coursemap-listing__title,
.coursemap-listing__subtext {
  color: #77B02A; }

.coursemap-listing__icon {
  position: absolute;
  top: 20px;
  right: 20px; }

.coursemap-listing__number {
  width: 50px;
  height: 50px;
  border: 1px solid #77B02A;
  color: #77B02A;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center; }
